import { useState } from "react";
import Galaxia from "../../img/galaxia.jpg";
import Image from "next/image";

export default function BlogHome() {
  // Estado para la categoría seleccionada
  const [categoriaSeleccionada, setCategoriaSeleccionada] = useState(null);

  // Datos estáticos de categorías
  const categorias = [
    { id: "creatividad", nombre: "Creatividad", cantidad: 1 },
    { id: "drupal", nombre: "Drupal", cantidad: 1 },
    { id: "github", nombre: "Github", cantidad: 1 }
  ];

  // Datos estáticos de etiquetas
  const etiquetas = [
    { id: "aprendizaje", nombre: "Aprendizaje" },
    { id: "bloqueos-creativos", nombre: "Bloqueos creativos" },
    { id: "comandos", nombre: "Comandos" },
    { id: "consejos", nombre: "Consejos" },
    { id: "drupal", nombre: "Drupal" }
  ];

  // Datos estáticos de artículos
  const articulos = [
    {
      id: 1,
      titulo: "Horror vacui",
      imagen: Galaxia,
      fecha: "25 de feb, 2020",
      categoriaId: "creatividad",
      categoria: "Creatividad",
      descripcion: "Crear es la acción a través de la cual el ser humano comprende su esencia divina; sin embargo, la creatividad demanda de algunos cuidados y ejercicios para evadir la indigestión mental que acaba en la nada."
    },
    {
      id: 2,
      titulo: "Comandos utiles github",
      imagen: Galaxia,
      fecha: "25 de feb, 2020",
      categoriaId: "github",
      categoria: "Github",
      descripcion: "En nuestro diario vivir como desarrolladores necesitamos la ayuda de GIT, este es un listado de comando que pueden ser de gran ayuda"
    }
  ];

  // Función para manejar el clic en una categoría
  const handleCategoriaClick = (categoriaId) => {
    // Si ya está seleccionada, la deseleccionamos
    if (categoriaSeleccionada === categoriaId) {
      setCategoriaSeleccionada(null);
    } else {
      setCategoriaSeleccionada(categoriaId);
    }
  };

  // Función para mostrar todas las categorías
  const mostrarTodas = () => {
    setCategoriaSeleccionada(null);
  };

  // Filtramos los artículos según la categoría seleccionada
  const articulosFiltrados = categoriaSeleccionada
    ? articulos.filter(articulo => articulo.categoriaId === categoriaSeleccionada)
    : articulos;

  return (
    <div className="principal-blog">
      <div className="principal-blog-header">
        <div className="principal-blog-header-items">
          <div className="principal-blog-header-title">
            <h1 className="principal-blog-title">Blog</h1>
            <h2 className="principal-blog-subtitle">
              Nuestro conocimiento en tus manos
            </h2>
          </div>
          <div className="principal-blog-breadcrumb"></div>
        </div>
      </div>
      <div className="principal-blog-labels-and-articles">
        <div className="principal-blog-labels">
          <div className="principal-blog-labels-categories">
            <h1 className="principal-blog-labels-categories-title">
              Categorias
            </h1>
            <div className="principal-blog-labels-categories-items">
              {categorias.map((cat) => (
                <div 
                  key={cat.id}
                  className={`principal-blog-label-categories-item ${
                    categoriaSeleccionada === cat.id ? "active" : ""
                  }`}
                >
                  <a 
                    className="principal-blog-label-categories-link"
                    onClick={() => handleCategoriaClick(cat.id)}
                    style={{ cursor: "pointer" }}
                  >
                    {cat.nombre}
                    <span className="principal-blog-label-categories-quantity">
                      -{cat.cantidad}
                    </span>
                  </a>
                </div>
              ))}
              <div className="principal-blog-label-categories-item-all">
                <a 
                  className="principal-blog-label-categories-link"
                  onClick={mostrarTodas}
                  style={{ cursor: "pointer" }}
                >
                  Todas las categorias
                </a>
              </div>
            </div>
          </div>
          <div className="principal-blog-labels-tag">
            <h1 className="principal-blog-labels-tag-title">Etiquetas</h1>
            <div className="principal-blog-labels-tag-items">
              {etiquetas.map(tag => (
                <div key={tag.id} className="principal-blog-label-tag-item">
                  <a className="principal-blog-label-tag-link">{tag.nombre}</a>
                </div>
              ))}
            </div>
          </div>
        </div>
        <div className="principal-blog-articles">
          {/* Mostrar mensaje si no hay artículos para la categoría seleccionada */}
          {articulosFiltrados.length === 0 && (
            <div className="principal-blog-no-articles">
              <p>No hay artículos disponibles para esta categoría.</p>
            </div>
          )}
          
          {/* Mostrar artículos filtrados */}
          {articulosFiltrados.map((articulo) => (
            <a key={articulo.id} className="principal-blog-article">
              <div className="principal-blog-article-title">{articulo.titulo}</div>
              <div className="principal-blog-article-image">
                <Image
                  className="principal-blog-image"
                  src={articulo.imagen}
                  alt={articulo.titulo}
                  width={200}
                  height={64}
                  priority
                />
                <div className="principal-blog-article-date">
                  <p>{articulo.fecha}</p>
                </div>
              </div>
              <div className="principal-blog-article-label">
                <a>{articulo.categoria}</a>
              </div>
              <div className="principal-blog-article-description">
                <p>{articulo.descripcion}</p>
              </div>
              <div className="principal-blog-article-see-more">
                <a>Ver mas</a>
              </div>
            </a>
          ))}
        </div>
      </div>
    </div>
  );
}